.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
.blank{
  background-color:rgb(138, 83, 83) ;
  height: 18vh;
  width: 100%;
  text-align: center;
  padding-top: 20px;
  color: rgb(185, 218, 228);
  text-transform: uppercase;
  font: bold;
  
}
.bodyy{
  background-color:rgb(191, 210, 211);
  height: 60vh;
  width: 100%;
  text-align: center;
  color: blue;
  padding-top: 10%;
}
.footer{
  background-color: bisque;
  height: 20vh;
  width: 100%;
  text-align: center;
  margin: 10px;
}
.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
